body, html {
  height: 100%;
}

.bgm-1, .bgm-2, .bgm-3, .bgm-4 {
  height: 100vh;
  position: relative;

  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.bgm-1 {
  background-image: url("http://data.1freewallpapers.com/download/bright-light-on-the-field.jpg");
}

.bgm-2 {
  background-image: url("https://thewallpaper.co/wp-content/uploads/2016/03/starry-night-wide-high-resolution-wallpaper-for-desktop-background-download-pictures-hd-images-cool-images-widescreen-1920x1080.jpg")
}

.bgm-3 {
  background-image: url("http://wallpapercave.com/wp/RpGyOy4.jpg")
}

.bgm-4 {
  background-image: url("https://thewallpaper.co/wp-content/uploads/2016/02/blue-sea-hd-sea-wallpapers-summer-sun-sky-fresh-air-amazing-beach-swimming-widescreen-1805x1015.jpg")
}

.caption {
  position: absolute;
  left: 0;
  top: 85%;
  width: 100%;
  text-align: center;
  /* color: #000; */
}

.captionText {
  background-color: #ffffff;
  color: rgb(31, 2, 2);
  opacity: 0.3;
  padding: 18px;
  font-size: 25px;
  letter-spacing: 10px;
}


@-webkit-keyframes downarrow {
  0% { -webkit-transform: translateY(0); opacity: 0.1 }
  100% { -webkit-transform: translateY(0.4em); opacity: 0.5 }
}

.arrow {
  border-color:transparent;
  border-style:solid;
  border-width:0 2em;
  display:block;
  /* height:0; */
  top: 92vh;
  opacity:0.4;
  text-indent:-9999px;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  left: 47.5%;
  position: absolute;
}

.down {
  -webkit-animation: downarrow 3s infinite alternate ease-in-out;
  border-top:2em solid #fdaa5b;
  /* border-top:2em solid #00b6f1; */
}



