table {
    width:93%;
    margin: 0 auto;
  }

  #scheduleTable th:not(.timeTitle), #scheduleTable td:not(.timeTitle) {
    border: 1px solid rgb(185, 224, 250);
    border-collapse: collapse;
  }

/* table, th, td {
    border: 1px solid rgb(228, 226, 226);
    border-collapse: collapse;
  } */

  tr {
    
  }

  td {
    text-align: left;
    height: 28px;
    padding: 0px;
  }

  th {
    text-align: center;
    height: 28px;
    padding: 0px;
  }

  #scheduleTable tr:nth-child(even) {
    background-color: rgb(230, 243, 252);
  }

  #scheduleTable tr:nth-child(odd) {
   background-color: rgb(243, 250, 252);
  }
  
  table th {
    background-color: black;
    color: white;
  }

  .cellText {
    padding: 0 0 0 10px;
    height: 28px;
    overflow: hidden;
  }
  
  .timeTitle {
    /* border-style: none solid none none; */
    /* border: 0px none white; */
    background-color: white;
  }

  .timeTitleText {
    -webkit-transform: translate(-5%, -70%);
            transform: translate(-5%, -70%);
    text-align: right;
    color: rgb(2, 128, 167);
  }

  .orange {
    background-color: orange;
    color: white;
  }

  select {
    margin: 5px;
  }

  #scheduleModifier {
    background-color: rgb(127, 229, 255);
    padding: 10px;
    border-radius: 10px;
  }

  .scheduleModifier {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%,-50%);
  }

  .hidden {
    display: none;
  }

  #userInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  #userInfo * {
    margin: 5px;
  }

  textarea {
    width: 50%;
  }

  button {
    width: 80px;
    margin:5px;
    border-radius: 8px;
  }

  
  
  

